import { Component, OnInit, AfterViewInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { FacilityService } from 'src/app/api/services';
import { untilDestroyed } from 'src/app/decorator';
import { isNullOrUndefined } from 'util';
import { FacilityServiceService } from '../../service/facility-service.service';

@Component({
  selector: 'app-facility-main',
  templateUrl: './facility-main.component.html',
  styleUrls: ['./facility-main.component.scss']
})
export class FacilityMainComponent implements OnInit,AfterViewInit, OnDestroy {
  Mode: string;
  __subscription: any = {};
  constructor(
    private facilityservice : FacilityServiceService, 
    private cd: ChangeDetectorRef) { }

  ngOnInit(): void {
  }
  ngAfterViewInit(): void {
    this.facilityservice.Mode.pipe(untilDestroyed(this)).subscribe(data => {
        if (!isNullOrUndefined(data)) {
            this.Mode = data.mode;
            this.cd.markForCheck();
        }
    }); 
  }
  ngOnDestroy(): void {
    this.facilityservice.Mode.next({ mode: 'List',data:null });
}
}
