import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { FacilityService } from '../../../../api/services/facility.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';

import * as _ from 'underscore';
import { FacilityServiceService } from '../../service/facility-service.service';

@Component({
  selector: 'app-facility-setup',
  templateUrl: './facility-setup.component.html',
  styleUrls: ['./facility-setup.component.scss']
})
export class FacilitySetupComponent implements OnInit {

  facility:any;
  
  isFacilityCategoryLoaded:boolean = false;

  isEditFacilityCategory:boolean = false;

  modalService:any;

  facilityCategoryData:any

  isFacilityCategoryNew:boolean = false;
  isFacilityCategoryUpdate:boolean = false;

  isSlotNew:boolean = false;

  isCategoryError:boolean = false;
  alertMessage:string = "";

  faclityRateBaseId:any;
  faclityTypeId:any;

  facilitySlotLength:number;

  facilityAmount:string = "";

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private facilityService:FacilityService,
    private facilitylocalservice:FacilityServiceService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }


 


 
  addNewFacilityCategory(){
    // this.isFacilityCategoryNew = true;
    // this.isSlotNew = false;
    // this.facility = {};
    var item = {
      isFacilityCategoryNew:true,
      isSlotNew : false,
      isFacilityCategoryUpdate:false,
      isFacilityViewAlone : false,
      facility: {}
    };
    this.facilitylocalservice.Mode.next({ mode: 'Entry',data:item });
  }

  addNewSlot(item){
    // this.isFacilityCategoryUpdate = true;
    // this.isFacilityCategoryNew = false;
    // this.isSlotNew = true;
    // this.updatefacilityData(item);
    var itemData = {
      isFacilityCategoryNew:true,
      isSlotNew : true,
      facility: item,
      isFacilityCategoryUpdate:false,
      isFacilityViewAlone : false
    };
    this.facilitylocalservice.Mode.next({ mode: 'Entry',data:itemData });
  }
  viewFacility(item){
    var itemData = {
      isFacilityCategoryNew:false,
      isSlotNew : false,
      facility: item,
      isFacilityViewAlone : true,
      isFacilityCategoryUpdate:true
    };
    this.facilitylocalservice.Mode.next({ mode: 'Entry',data:itemData });
  }

  updateFacilityCategory(item){
    // this.isFacilityCategoryUpdate = true;
    // this.isFacilityCategoryNew = false;
    // this.isSlotNew = false;
    var itemData = {
      isFacilityCategoryNew:false,
      isSlotNew : false,
      facility: item,
      isFacilityViewAlone : false,
      isFacilityCategoryUpdate:true
    };
    this.facilitylocalservice.Mode.next({ mode: 'Entry',data:itemData });

  }
  removeCategoryBox(){
    this.isFacilityCategoryNew = false;
    this.isFacilityCategoryUpdate = false;
    this.isCategoryError = false;
  }

  getRateBase(id){
    if(id == 138){
      return 'Hour';
    }
    else {
      return 'Day';
    }
  }

  getSlots(items){
    var slots = items.filter( item => {
      return item.isActive;
    });
    return slots;
  }

  ngOnInit() {
    this.removeCategoryBox();
  	this.facility = {};
    this.facility.description = "";
    

    this.facility.rateBaseId = "";
    this.facility.facilityTypeId = "";

    this.facilityService.getApartmentFacilitiesByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
    this.facilityCategoryData = res.filter(item => {
        return item.isActive;
      })

      this.isFacilityCategoryLoaded = true;
    },
    error => {

    }); 
  }

}
